import "../styles.scss";
import Head from "next/head";
import Container from "../components/Container";
import Home from "../components/Home";
import About from "../components/About";

const Index = () => {
  // state = { siteActive: "about" };
  const [state, setState] = useState();

  const siteChange = site => {
    this.triggerSiteChange(site);
  };

  const triggerSiteChange = site => {
    if (site === state.siteActive) return;
    const changer = document.querySelector(".site-change");
    changer.classList.add("site-change--active");

    setTimeout(() => {
      setState({ siteActive: site });

      setTimeout(() => {
        changer.classList.remove("site-change--active");
      }, 1000);
    }, 1500);
  };

  const renderPage = () => {
    switch (state.siteActive) {
      case "/":
        return <Home siteChange={siteChange} />;
      case "about":
        return <About siteChange={siteChange} />;
      default:
        return <Home siteChange={siteChange} />;
    }
  };

  return (
    <div>
      <Head>
        <title>Rafał Cieślik Portfolio</title>
        <meta name="viewport" content="initial-scale=1.0, width=device-width" />
      </Head>
      <Container siteChange={siteChange}>
        {renderPage()}
        <div className="site-change" />
      </Container>
    </div>
  );
};

export default Index;
